:root {
  --link-height: 60px;
  --primary-color: #000;
  --accent-color: #f60;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  display: flex;
}

.nav__list {
  list-style: none;
  display: flex;
}

.nav__list li + li {
  margin-left: 20px;
}

.nav__list a {
  height: var(--link-height);
  color: var(--primary-color);
  align-items: center;
  padding: 0 5px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__list a.active:after {
  content: "";
  background-color: var(--accent-color);
  width: 100%;
  height: 3px;
  position: absolute;
  bottom: 0;
  left: 0;
}
